* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f9f9f9;
  padding: 0 1rem;
}

a {
  text-decoration: none;
  color: #3498db;
}

a:hover {
  color: #2980b9;
}

/* Container */
.container {
  max-width: 1100px;
  margin: 0 auto;
  overflow: hidden;
  padding: 0 2rem;
}

/* Header */
header {
  background: #333;
  color: #fff;
  padding: 1rem 0;
  text-align: center;
}

header h1 {
  font-size: 2.5rem;
}

header p {
  font-size: 1.1rem;
  margin-top: 0.5rem;
}

/* Navigation */
nav {
  display: flex;
  justify-content: center;
  background: #444;
  padding: 0.5rem 0;
}

nav ul {
  list-style: none;
  display: flex;
}

nav ul li {
  margin: 0 1rem;
}

nav ul li a {
  color: #fff;
  font-size: 1.1rem;
}

nav ul li a:hover {
  color: #3498db;
}

/* Showcase Section */
.showcase {
  text-align: center;
  margin: 2rem 0;
}

.showcase h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.showcase p {
  font-size: 1.2rem;
  color: #666;
}

/* About Section */
.about {
  background: #eaeaea;
  padding: 2rem;
  text-align: center;
}

.about h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.about p {
  font-size: 1.1rem;
  margin: 0.5rem 0;
}

/* Portfolio Section */
.portfolio {
  padding: 2rem 0;
  text-align: center;
}

.portfolio h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.portfolio .grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.portfolio .grid-item {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.portfolio .grid-item img {
  width: 100%;
  height: auto;
}

.portfolio .grid-item h3 {
  font-size: 1.5rem;
  margin: 1rem 0;
}

.portfolio .grid-item p {
  padding: 0 1rem 1rem;
}

/* Contact Section */
.contact {
  background: #333;
  color: #fff;
  padding: 2rem 0;
  text-align: center;
}

.contact h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.contact p {
  font-size: 1.1rem;
  margin: 0.5rem 0;
}

.contact a {
  color: #3498db;
  font-size: 1.1rem;
}

.contact a:hover {
  color: #2980b9;
}

/* Footer */
footer {
  background: #222;
  color: #fff;
  text-align: center;
  padding: 1rem 0;
  margin-top: 2rem;
}

footer p {
  font-size: 0.9rem;
}

footer a {
  color: #3498db;
}

footer a:hover {
  color: #2980b9;
}

/* Media Queries */
@media (max-width: 768px) {
  nav ul {
    flex-direction: column;
    text-align: center;
  }

  nav ul li {
    margin: 0.5rem 0;
  }
}